body {
  box-sizing: border-box;
  color: #060b35;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 10px;
  line-height: 18px;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 198px;
  height: 406px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  padding: 33px 20px 0;
  display: flex;
}

.card__descript {
  width: 166px;
  height: 36px;
  margin-bottom: 16px;
  padding: 40px 16px 0;
}

.card__descript-name {
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__descript-code {
  color: #616070;
  margin: 0;
  padding-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 18px;
  padding-top: 24px;
  display: flex;
}

.card__price-name {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  width: 166px;
  padding: 16px;
  display: block;
}

.card__buy-link {
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: inline-block;
}

.card__buy-link:hover {
  color: #00acdc;
