  height: 16px;
  padding: 16px 16px 0;
  display: flex;
}

.stars__container {
  display: flex;
}

.stars__star:before {
  content: "";
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  width: 16px;
  height: 16px;
  display: flex;
}

.stars__star:nth-child(-n+4):before {
  background-image: url("star-active.edda4c43.svg");
  background-size: 16px 16px;
}

.stars__star {
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__review {
  text-align: right;
